.company-addresses {
  margin-top: 40px;
}
.company-addresses .company-address-col {
  margin-top: 15px;
}
.company-addresses .company-address-col:hover .company-address-row-edit {
  opacity: 1;
}
.company-addresses .company-address-row-edit {
  opacity: 0;
  cursor: pointer;
  display: flex;
  width: 30px;
  height: 25px;
  justify-content: center;
  align-items: center;
}
.company-addresses .company-address-row-edit svg {
  color: #989CA8;
}
.company-addresses .company-address-row-title {
  font-weight: 500;
  font-size: 14px;
  color: #0d1b2a;
  letter-spacing: 0;
  display: flex;
}
.company-addresses .company-address-row-title span {
  flex: 1 1;
}
.company-addresses .company-address-row {
  font-weight: 500;
  font-size: 14px;
  color: #989CA8;
  letter-spacing: 0;
}

.company-infos-subtitle {
  font-weight: 700;
  font-size: 10px;
  color: #8a8a8a;
  letter-spacing: 0;
  display: block;
  text-transform: uppercase;
  margin-top: 33px;
}
.company-infos-value {
  font-weight: 400;
  font-size: 16px;
  color: #0d1b2a;
  letter-spacing: 0;
  display: block;
}

.partner-header {
  margin-bottom: 32px;
}

.partner-detail {
  margin-bottom: 10px;
}

.partner-detail-label {
  text-transform: uppercase;
  font-weight: 600;
  font-size: 10px;
  color: #8A8A8A;
  letter-spacing: 0;
}

.partner-detail-value {
  font-weight: 500;
  font-size: 16px;
  color: #0D1B2A;
  letter-spacing: 0;
}

.partner-addresses {
  margin-top: 40px;
}

.partner-address-row {
  font-weight: 500;
  font-size: 14px;
  color: #989CA8;
  letter-spacing: 0;
}

.partner-contacts {
  margin-top: 20px;
}

.partner-contacts .ant-card {
  cursor: pointer;
}

